.product {
  padding: 0.5rem;
  height: auto;
  font-family: "IM Fell Great Primer", serif;
  @media (min-width: 768px) {
    display: grid;
    grid-template-columns: repeat(10, minmax(0, 1fr));
    .image-container {
      grid-column: 1 / 6;
      grid-row: 1 / 3;
    }
    .details {
      grid-column: 6 / -1;
    }
    .shop {
      grid-column: 6 / -1;
      display: flex;
      justify-content: center;
    }
  }
  .image-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 2fr;
    place-items: center;
    gap: 0.1rem;
    justify-content: center;
    width: 100%;
    .img1 {
      grid-column: 1 / 2;
      grid-row: 1 / 3;
    }
    .img3 {
      grid-column: 2 / 3;
      grid-row: 1 / 3;
    }
    .img2 {
      grid-column: 1 / -1;
      grid-row: 3 / -1;
    }
    .img1,
    .img2,
    .img3 {
      width: 100%;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .details {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    gap: 2rem;
    text-align: center;
    width: 100%;
    p {
      line-height: 2;
    }
  }
  .size-list {
    display: flex;
    align-items: center;
    width: 100%;
    form {
      display: flex;
      justify-content: center;
      position: relative;
      margin-inline: auto;
      transition: all 0.5s linear;
      width: 100%;
      li {
        margin: 10px;
        position: relative;
        width: 100%;
        height: 3.5rem;
        transition: all 0.5s linear;

        input {
          opacity: 0;
          position: absolute;
          width: 100%;
          height: 100%;
        }

        label {
          display: inline-block;
          border-radius: 4px;
          width: 100%;
          height: 100%;
          display: grid;
          place-items: center;
        }

        input:checked + label {
          border: 1px solid;
        }
      }
      .submit-btn {
        position: absolute;
        top: 100%;
        border: 1px solid black;
        box-shadow: 5px 5px 2px 1px black;
        border-radius: 4px;
        transition: all 0.1s linear;
        &:hover {
          transform: translateY(-3px) translateX(-3px);
          box-shadow: 8px 8px 2px 1px black;
        }
        &:active {
          transform: translateY(3px) translateX(3px);
          box-shadow: 2px 2px 2px 1px black;
        }
        @media (min-width: 768px) {
          box-shadow: none;
          &::before {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            left: -0.6rem;
            top: -0.5rem;
            z-index: -1;
            background-color: #A27B5C;
            border-radius: 3px;
            transition: all 200ms ease-out;
          }

          &:hover::before {
            top: 0;
            left: 0;
          }
          &:active {
            transform: translateY(3px);
          }
        }
      }
    }
  }
}
